<template>
  <div>
    <!--<div class="topTitle-wrap">-->
    <!--<div>-->
    <!--<span class="tpTitle">整体报告</span>-->
    <!--</div>-->
    <!--<div class="">-->
    <!--<div class="selectStartEndDate">选择开始与结束时间：</div>-->
    <!--<DatePicker class="myDatePicker" type="daterange" @on-change="startTime" :value="datesValue" :options="optionsDisable" split-panels placeholder="Select date" style="width: 200px"></DatePicker>-->
    <!--<Button class="btnColor" style="margin-right:50px;" icon="archive" type="Text" @click="confrimDownLoad">下载报告</Button>-->
    <!--</div>-->
    <!--</div>-->


    <div class="topTitle-wrap">
      <div>
        <span class="tpTitle">整体报告</span>
      </div>
      <div class="" style="margin-right: 20px;">
        <ul class="myDatePicker" slot="mySolt">
          <li class="myDatePicker_li">
            <span>日期范围</span>
            <DatePicker @on-change="startTime" :value="dateRange" type="daterange" placeholder=""
                        placement="bottom-end"
                        :clearable=false :editable=false
                        style="width: 200px;"></DatePicker>
          </li>
          <li class="myDatePicker_li">
            <Button class="btnColor" type="primary" icon="archive" @click="confrimDownLoad">下载报告</Button>
          </li>
        </ul>
      </div>
    </div>


    <div class="fu">
      <div>
        <ul class="top-wrap">
          <li>
            <p>广告展现量</p>
            <p class="count">{{adShowNum}}</p>
          </li>
          <li>
            <p>广告点击量</p>
            <p class="count">{{adClickNum}}</p>
          </li>
          <li>
            <p>广告点击率</p>
            <p class="count">{{adRate}}</p>
          </li>
        </ul>
      </div>

      <div id="main" style="justify-content:center;display: flex"></div>
      <div class="ract-wrap">
        <div @click="showNum">
          <span class="showRact"></span>
          <span>展现量</span>

        </div>
        <div class="ract-wrap" @click="clickNum">
          <span class="clickRact"></span>
          <span>点击量</span>

        </div>
      </div>
      <Table border :columns="reportColumns" :data="adReportList"></Table>

      <Page :total="totalCount" :current="pageNo" show-sizer show-total
            @on-change="changePageNo" @on-page-size-change="changePageSize"></Page>

    </div>


  </div>

</template>
<style scoped>

  .top-wrap {
    display: flex;
    justify-content: space-around;
  }

  .top-wrap li {
    text-align: center;
    /*border: 1px solid #d7f0f2;*/
    padding: 10px;
    color: #c5c5c5;
  }

  .count {
    font: 22px '微软雅黑', Arial, '宋体', Helvetica, sans-serif;
    color: #555;
  }

  #main {
    width: 90%;
    height: 400px;
  }

  .showRact, .clickRact {
    display: inline-block;
    width: 30px;
    height: 15px;
  }

  .showRact {
    background: #044b7c;
  }

  .clickRact {
    background: #ea1107;
  }

  .ract-wrap {
    text-align: center;
    margin-bottom: 10px;
  }

  .ract-wrap div {
    display: inline-block;

  }

  .ract-wrap div span {
    display: inline-block;
    vertical-align: middle;
  }

  .ract-wrap div:nth-child(1) {
    margin-right: 100px;
  }

  .myDatePicker, .myDatePicker_li {
    display: inline-block;
    color: black;
  }

  .myDatePicker {

  }

  .myDatePicker_li {

  }

  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }
</style>
<script>
  import topTitle from '@/components/common/topTitle'
  import echarts from 'echarts'


  export default {
    components: {topTitle},
    data() {
      return {
        adClickNum: '0',
        adShowNum: '0',
        adRate: '0',
        startDay: '',
        endDay: '',
        pageSize: 10,
        pageNo: 1,
        totalCount: 0,
        dataClickNum: [],
        dataShowNum: [],
        dataAdDateDay: [],
        datesValue: [],
        myChart: '',
        chartArr: [],
        chartColor: '#d6eaff',
        downloadUrl: '',
        modal1: false,
        bolChart: false,
        optionsDisable: {
          disabledDate(date) {
            let initdate = new Date() - 86400000 * 30;//前一个月时间
            return (date && date.valueOf() < initdate) || (date && date.valueOf() >= new Date())
          }
        },
        reportColumns: [
          {
            title: '时间',
            key: 'adDateDay'
          },
          {
            title: '展现量',
            key: 'showNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.showNum))
            }
          },
          {
            title: '点击量',
            key: 'clickNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.clickNum))
            }
          }
          ,
          {
            title: '点击率',
            key: 'rate'
          }
        ],
        adReportList: []

      }
    },
    created() {

    },
    mounted() {
      this.startDay = this.getPreDate(-7);//获取一周的时间
      this.endDay = this.getPreDate(-1);
      this.dateRange = [this.startDay, this.endDay];
      this.myChart = echarts.init(document.getElementById('main'));
      this.getOverallTotalReport();
      this.getOverallReportChart();
      this.getOverallReportList();


    },
    methods: {

      initEcharts() {

        var option = {
          title: {
            text: ''
          },

          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.dataAdDateDay,
          },

          color: ["#044b7c", "#ea1107"],

          // yAxis: {
          //   type: 'value'
          // },

          yAxis: [
            // type: 'value'
            {
              name: '展现量',
              type: 'value'
            },
            {
              name: '点击量',
              type: 'value',

            }
          ],
          minInterval: 1,
          series: [
            {
              name: '展现量',
              data: this.dataShowNum,
              type: 'line',
              areaStyle: {
                color: '#d6eaff',
              },
              lineStyle: {
                color: '#044b7c',
              },

            },
            {
              name: '点击量',
              data: this.dataClickNum,
              yAxisIndex: 1,
              type: 'line',
              areaStyle: {
                color: '#ffd6e1',
              },
              lineStyle: {
                color: '#ea1107',
              },


            }
          ]
        };

        // 使用刚指定的配置项和数据显示图表。
        this.myChart.setOption(option);
      },

      splitNum(num) {
        var reg = /(\d)(?=(?:\d{3})+$)/g;
        var dec = (num + '').split('.');
        if (dec.length == 2) {
          return dec[0].replace(reg, '$1,') + '.' + dec[1];
        } else {
          return dec[0].replace(reg, '$1,');
        }
      },
      getOverallTotalReport() {
        const url = `/report/getOverallTotalReport.json?startDay=${this.startDay}&endDay=${this.endDay}`;
        this.$Http.get(url, {}).then(res => {
          console.log(res)
          if (res.body.status == 0) {
            let adReport = res.body.data.result;
            if (adReport != "") {
              this.adClickNum = this.splitNum(adReport.clickNum);
              this.adShowNum = this.splitNum(adReport.showNum);
              this.adRate = adReport.rate;
            }

          }
        });
      },
      getPreDate(days) {
        let date = new Date();//获取当前时间
        date.setDate(date.getDate() + days);//设置天数 -1 天
        return date.Format("yyyy-MM-dd");
      },

      getOverallReportChart() {
        const url = `/report/getOverallReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}`;
        this.$Http.get(url, {}).then(res => {
          // console.log(res)
          if (res.body.status == 0) {
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            this.dataAdDateDay = [];
            this.dataClickNum = [];
            this.dataShowNum = [];
            chartList.forEach((item, index) => {
              console.log(item.clickNum)
              console.log(item.showNum)
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay)
            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;
            // if (this.bolChart == false) {
            //     this.chartArr = this.dataShowNum
            //     this.chartColor = "#d6eaff";
            // }else{
            //     this.chartArr = this.dataClickNum
            //     this.chartColor = "#ffd6e1";
            // }
            // this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();
          }
        });
      },

      confrimDownLoad() {
        const token = sessionStorage.getItem('token');
        this.downloadUrl = this.ApiUrl + `/report/downloadAllReport.json?startDay=${this.startDay}&endDay=${this.endDay}&t=${token}`;
        window.location.href = this.downloadUrl;
      },

      getOverallReportList() {
        const url = `/report/getOverallReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&pageSize=${this.pageSize}&pageNo=${this.pageNo}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            let chartList = res.body.data.result;
            this.totalCount = res.body.data.totalCount;
            let clickNumArr = [];
            let showNumArr = [];
            this.adReportList = [];
            chartList.forEach((item, index) => {
              this.adReportList.push(item);
            })

          }
        });
      },

      changePageNo: function (pageNo) {

        console.log("pageNo==" + pageNo)

        //this.pageSize=10;
        this.pageNo = pageNo;
        this.getOverallReportList();

      },
      changePageSize: function (pageSize) {

        console.log("pageSize==" + pageSize)

        this.pageNo = 1;
        this.pageSize = pageSize;
        this.getOverallReportList();

      },
      // startTime(data) {
      //   console.log(data);
      //   this.startDay = data[0];
      //   this.endDay = data[1];
      //   this.getOverallReportList();
      //   this.getOverallReportChart();
      // },

      startTime(data) {
        if (data.length < 2 || data[0] == '' || data[1] == '') {
          this.$Message.error('请选择日期范围');
          return false;
        }
        this.startDay = data[0];
        this.endDay = data[1];
        this.pageNo = 1;
        this.getOverallTotalReport();
        this.getOverallReportList();
        this.getOverallReportChart();

      },

      showNum() {
        // this.bolChart = false;
        // this.chartArr = this.dataShowNum
        // this.myChart.clear();
        // this.chartColor = "#d6eaff";
        // this.initEcharts();
      },
      clickNum() {
        // this.bolChart = true;
        // this.chartArr = this.dataClickNum
        // this.myChart.clear();
        // this.chartColor = "#ffd6e1";
        // this.initEcharts();
      }
    }
  }
</script>



